
import { Component, OnInit, OnDestroy , Input} from '@angular/core';
import {ComponentInteractionService} from "@services/component-interaction.service";
import { FormBuilder, FormGroup,FormControl , Validators } from '@angular/forms';
import {PrepareFinalForm} from "../../../../../../core/_utils/prepareFinalForm";
import {MatSnackBar} from '@angular/material/snack-bar';
import { Countries } from '../../../../../../core/_utils/countries';
import { Observable } from 'rxjs';
import { startWith, map } from 'rxjs/operators';
import {VitamindService} from "@services/vitamind.form.service";
import {SplashScreenService} from "@services/splash-screen-service";
const AVAILABLE_NATIONALITIES: any[] = Countries;
@Component({
  selector: 'kt-diabetes-family-history',
  templateUrl: './family-history.component.html'
})
export class DiabetesFamilyHistoryComponent implements OnInit {
  Mform:FormGroup;
  title:string = "Family Information";
  @Input('formData') formId:any;
  formData:any;
  filteredNationalities: Observable<string[]>;
  filteredPlaces: Observable<string[]>;
  formAttributes:any;
  @Input('disableInput') disableInput:boolean;
  saveFormId : any = 0;
  startDate = new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate());
  maxDate = new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate());
  isFormSubmit : boolean = false;
  constructor(
      private _service:VitamindService,
      private eformFB:FormBuilder,
      private _interactionService:ComponentInteractionService,
      private _snackBar: MatSnackBar,
      private splashService : SplashScreenService,
      private finalFormValues:PrepareFinalForm
  ) { }

  ngOnInit() {
    this.createForm();
    if (!this.formId)
      this._interactionService._Refid$.subscribe((id)=> {
        if(id) {
          this.Mform.patchValue({"savedFormID": id})

        }
      })
    else {
      this.splashService.splashScreen({ isLoading : true, message : "LOADING" })
      this._service.getQuestionnaire(this.formId,12).subscribe((res:any[])=> {
        this.formData = res;
        this.saveFormId = this.formId;
        this.splashService.splashScreen({isLoading : false, message : "" })
        if(res.length)
        this.prepareForm();
      })
    }
  }

  createForm() {
    this.Mform = this.eformFB.group({
      "savedFormID": new FormControl({value: 0, disabled: this.disableInput}),
      "fam_fatethnicity": new FormControl({value: '', disabled: this.disableInput}),
      "fam_fatethnicity_other": new FormControl({value: '', disabled: this.disableInput}),
      "fam_fatplaceofbirth": new FormControl({value: '', disabled: this.disableInput}),
      "fam_motethnicity": new FormControl({value: '', disabled: this.disableInput}),
      "fam_motethnicity_other": new FormControl({value: '', disabled: this.disableInput}),
      "fam_motplaceofbirth": new FormControl({value: '', disabled: this.disableInput}),
      "fam_parents_related": new FormControl({value: '', disabled: this.disableInput}),
      "fam_parents_related_yes": new FormControl({value: '', disabled: this.disableInput}),
      "fam_hypertension": new FormControl({value: '', disabled: this.disableInput}),
      "fam_heart": new FormControl({value: '', disabled: this.disableInput}),
      "fam_dep": new FormControl({value: '', disabled: this.disableInput}),
      "fam_osteo": new FormControl({value: '', disabled: this.disableInput}),
      "fam_type1": new FormControl({value: '', disabled: this.disableInput}),
      "fam_type2": new FormControl({value: '', disabled: this.disableInput}),
      "fam_gesdia": new FormControl({value: '', disabled: this.disableInput}),
      "fam_cancer": new FormControl({value: '', disabled: this.disableInput}),
      "fam_condition_cancer_type": new FormControl({value: '', disabled: this.disableInput}),
      "fam_other": new FormControl({value: '', disabled: this.disableInput}),
      "fam_condition_other": new FormControl({value: '', disabled: this.disableInput}),
      "fam_Epilepsy": new FormControl({value: '', disabled: this.disableInput}),
      "fam_Bipolar": new FormControl({value: '', disabled: this.disableInput}),
      "fam_Schizophrenia": new FormControl({value: '', disabled: this.disableInput}),
      "fam_traumatic_stress": new FormControl({value: '', disabled: this.disableInput}),
      "fam_Asthma": new FormControl({value: '', disabled: this.disableInput}),
      "fam_Thyroid": new FormControl({value: '', disabled: this.disableInput}),
      "fam_Rheumatoid_Arthritis": new FormControl({value: '', disabled: this.disableInput}),
      "fam_Liver": new FormControl({value: '', disabled: this.disableInput}),
      "fam_High_cholesterol": new FormControl({value: '', disabled: this.disableInput}),
      "fam_Obesity": new FormControl({value: '', disabled: this.disableInput}),
      "fam_Alzheimer": new FormControl({value: '', disabled: this.disableInput}),
      "fam_Parkinsons": new FormControl({value: '', disabled: this.disableInput}),
      "med_prediabetes": new FormControl({value: '', disabled: this.disableInput}),
      "med_gestational_diabetes": new FormControl({value: '', disabled: this.disableInput}),
    });
    this.filteredNationalities = this.Mform.controls["fam_fatplaceofbirth"].valueChanges
    .pipe(
        startWith(''),
        map(val => { return this.filterNationalities(val != null && val != "" ? val.toString() : '') })
    );
    this.filteredPlaces = this.Mform.controls["fam_motplaceofbirth"].valueChanges
    .pipe(
        startWith(''),
        map(val => { return this.filterNationalities(val != null && val != "" ? val.toString() : '') })
    );
    this.getFormAttributeValues();
  }


  getFormAttributeValues() {
    this._service.getFormAttribute(12,3).subscribe((res)=> {
      this.formAttributes = res;
    })
  }

  prepareForm() {
    Object.keys(this.formData[0]).forEach(name => {
      if (this.Mform.controls[name]) {
        this.Mform.controls[name].patchValue(this.formData[0][name], {onlySelf: true});
      }
    });
  }


  createSampleId() {
    this.splashService.splashScreen({ isLoading : true, message : "SAVING" })
    this._service.createSampleId(12,0).subscribe((res)=> {
      this.saveFormId = res;
      this.splashService.splashScreen({isLoading : false, message : "" })
      this._interactionService.sendRefId(parseInt(this.saveFormId));
      this.onSubmit();
    });
  }

  onSubmit() {
    if (!this.Mform.value["savedFormID"] && !this.saveFormId) {
      this.createSampleId();
    } else {
      this.Mform.patchValue({"savedFormID" : this.Mform.value["savedFormID"] || this.saveFormId })

      let data = this.finalFormValues.prepareAttibuteForm(this.Mform.value, this.formAttributes, "savedFormID",this.formId)
      if (this.formId || this.isFormSubmit) {
        this.splashService.splashScreen({isLoading : true, message : "UPDATING" })
        this._service.getFormAttributeValues(this.formId || this.Mform.controls["savedFormID"].value).subscribe((res:any) => {
          if(res){
            for(var i=0;i<data.length;i++) {
              for (var j = 0; j < res.length; j++) {
                if(data[i]["formAttributeID"] == res[j]["FormAttributeID"]){
                  data[i]["FormAttributeValueID"] = res[j]["FormAttributeValueID"]
                }
              }
            }
            this._service.createSample8(data, true).subscribe((res)=> {
              if (res) {
                this.splashService.splashScreen({isLoading : false, message : "" })
                this._snackBar.open("Data has been updated successfully!", 'Ok', {
                  duration: 5000,
                  verticalPosition: 'bottom',
                  horizontalPosition: 'center'
                });
              }
            })

          }
        })
      } else {
        this.splashService.splashScreen({ isLoading : true, message : "INSERTING" })
        this._service.createSample8(data, this.formId ? true : false).subscribe((res)=> {
          if (res) {
            this.isFormSubmit = true;
            this.splashService.splashScreen({isLoading : false, message : "" })
            this._snackBar.open("Data has been inserted successfully!", 'Ok', {
              duration: 5000,
              verticalPosition: 'bottom',
              horizontalPosition: 'center'
            });
          }
        })
      }
    }
  }
  filterNationalities(val: string): string[] {
    return AVAILABLE_NATIONALITIES.filter(option =>
      option.toLowerCase().includes(val.toLowerCase()));
    }
}
